@function pxToRem($px) {
  $baseSize: 20;
  @return ($px / $baseSize)+rem;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input,
textarea {
  font: inherit;
  font-size: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

html,
body {
  width: 100%;
  height: 100%;
}

body,
textarea {
  font-family: 'PingFang SC', 'Helvetica Neue', Helvetica, Arial,
    'Hiragino Sans GB', 'Heiti SC', 'Microsoft YaHei', 'WenQuanYi Micro Hei',
    sans-serif;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent;
}
a,
div,
button,
input,
span,
img,
li {
  text-decoration: none;
}

img {
  width: 100%;
  border: none;
}

ul {
  list-style: none;
  border-width: 0;
}

.container {
  overflow-y: scroll;
  width: 100%;
  min-width: 100%;
  height: 100%;
  min-height: 100%;
}

.ellipsis {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}

.ellipsis2 {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
}

.ellipsis3 {
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
}

.header-banner{
  //高度
  height: pxToRem(44);
  //布局
  display: flex;
  justify-content: center;
  align-items: center;
  //字体
  font-family: PingFangSC-Medium;
  font-size: pxToRem(18);
  font-weight: 500;
  letter-spacing: 0;
  color: #000000;
}

.header-back{
    //大小
    width: pxToRem(20);
    height: pxToRem(20);
    //位置
    position: absolute;
    left: pxToRem(13);
    //背景
    background: url(../../public/images/back@2x.png) no-repeat center;
    background-size: 100% 100%;
}

.section{
    margin: 0 pxToRem(16);
    height: auto;
}

.header-title{
    font-family: PingFangSC-Medium;
    font-size: pxToRem(18);
    color: #000000;
    letter-spacing: 0;
    text-align: center;
    line-height: pxToRem(18);
    font-weight: 500;
    span{
        font-family: PingFangSC-Regular;
        font-size: pxToRem(14);
        color: #999999;
        text-align: center;
        line-height: pxToRem(16);
        font-weight: 400;
    }
}


.red{
    color: #E64545 !important;
}

.green{
    color: #3AA64C !important;
}

.banner-border{
    border-bottom: pxToRem(0.5) solid #E5E5E5
}

.hq-container{
    margin: pxToRem(4) pxToRem(16) 0 pxToRem(16);
    height: pxToRem(42);
    display: flex;
    align-items: center;
    .number-text{
        font-family: PingFangSC-Medium;
        font-size: pxToRem(14);
        font-weight: 500;
        margin-right: pxToRem(12);
    }
    .big{
        font-size: pxToRem(30);
        margin-right: pxToRem(20);
    }
}

.table-line{
    margin: 0 pxToRem(16);
    height: pxToRem(28);
    display: flex;
    font-family: PingFangSC-Regular;
    font-size: pxToRem(14);
    color: #666666;
    letter-spacing: 0;
    line-height: pxToRem(14);
    font-weight: 400;
    .table-cell{
        flex: 1;
        display: flex;
        align-items: center;
        .number{
            font-family: PingFangSC-Medium;
            font-size: pxToRem(15);
            color: #333333;
            letter-spacing: 0;
            line-height: pxToRem(15);
            font-weight: 500;
            margin-left: pxToRem(8);
        }
    }
}

.tab{
    display: flex;
    margin: pxToRem(9.5) 0;
    border-top-width: pxToRem(0.8);
    border-bottom-width: pxToRem(0.5);
    border-color: #E5E5E5;
    border-style: solid;
    li{
        //布局
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        //其它通用样式
        font-family: PingFangSC-Regular;
        font-size: pxToRem(16);
        color: #666666;
        letter-spacing: 0;
        text-align: center;
        line-height: pxToRem(16);
        span{
            //布局
            display: flex;
            align-items: center;
            position: relative;
            //其他样式
            height: pxToRem(48);
        }
        &.active {
            color: #00B893;
        }
        &.active {
            &:not(:last-child) span::after{
                content: '';
                position: absolute;
                bottom: 0;
                background-color: #00B893;
                width: 100%;
                height: pxToRem(2);
                border-radius: pxToRem(1);
            }
        }
        &:last-child{
            flex: 1.3
        }
    }
}

.chart-container{
    height: pxToRem(450);
    margin: 0 pxToRem(16);
}

.marginTop{
    margin: pxToRem(16);
}

.more{
    width: pxToRem(9);
    height: pxToRem(5);
    margin-left: pxToRem(4);
    background: url(../../public/images/more.png) no-repeat;
    background-position: center center;
    background-size: 100%;
}

.tab-container{
    width: 100%;
    position: relative;
    .drop{
        position: absolute;
        display: none;
        right: pxToRem(8);
        top: pxToRem(48);
        z-index: 999;
        background-color: #fff;
        ul{
            font-family: PingFangSC-Regular;
            font-size: pxToRem(14);
            color: #666666;
            letter-spacing: 0;
            text-align: center;
            margin: 0 pxToRem(10);
            li{
                width: pxToRem(65);
                line-height: pxToRem(40);
                border-bottom: pxToRem(0.5) solid #E5E5E5;
                &:last-child{
                    border-bottom-width: 0 !important;
                }
                &.active {
                    color: #00B893;
                }
            }
        }
        box-sizing: border-box;
        border: 0.0313rem solid #eee;
        box-shadow: 0 pxToRem(3) pxToRem(5) 0 rgba(0,0,0,0.15);
    }
}

